@import "src/common/styles/sass/mixins";
@import "src/common/styles/sass/variables";

.projectsBlock {
    .projectsContainer {
    @include objectsDirection(flex, flex-start, column);

    .projects {
      width: 100%;
      @include objectsDirection(flex, space-between, row);
      flex-wrap: wrap;
      margin-top: 30px;
      margin-bottom: $marginBottomBlock;
      @media screen and (max-width: 755px) {
        justify-content: center;
      }
    }
  }
}
