@import "src/common/styles/sass/variables.scss";
@import "src/common/styles/sass/mixins";

.contactsBlock{
    margin-bottom: $marginBottomBlock;
}

.contactsContainer{
    flex-direction: column;
    justify-content: flex-start;
}

.contactsForm {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.contactsForm input, .contactsForm textarea{
    width: 100%;
    margin-bottom: 30px;

}

.btn {
    @include btn()
}

@media screen and (max-width: 650px) {
    .contactsForm {
        width: 100%;
    }
}